import React, { Component } from 'react';
import { Popover, PopoverBody } from 'reactstrap';
import './social.scss';

const brocha = <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 640 512"><path d="M256.47 216.77l86.73 109.18s-16.6 102.36-76.57 150.12C206.66 523.85 0 510.19 0 510.19s3.8-23.14 11-55.43l94.62-112.17c3.97-4.7-.87-11.62-6.65-9.5l-60.4 22.09c14.44-41.66 32.72-80.04 54.6-97.47 59.97-47.76 163.3-40.94 163.3-40.94zM636.53 31.03l-19.86-25c-5.49-6.9-15.52-8.05-22.41-2.56l-232.48 177.8-34.14-42.97c-5.09-6.41-15.14-5.21-18.59 2.21l-25.33 54.55 86.73 109.18 58.8-12.45c8-1.69 11.42-11.2 6.34-17.6l-34.09-42.92 232.48-177.8c6.89-5.48 8.04-15.53 2.55-22.44z" /></svg>;

class Gift extends Component{
  constructor(props){
    super(props);
    this.state = {
      popGift: false,
    }
    this.handleGift = this.handleGift.bind(this);
  }

  handleGift() {
    const { popGift } = this.state;
    this.setState({ popGift: !popGift });
  }

  render() {
    const { popGift } = this.state;
    return (
      <div className="gift_cont">
        <button onClick={this.handleGift} id="gift" className="btn_social">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 512 512">
            <path d="M32 448c0 17.7 14.3 32 32 32h160V320H32v128zm256 32h160c17.7 0 32-14.3 32-32V320H288v160zm192-320h-42.1c6.2-12.1 10.1-25.5 10.1-40 0-48.5-39.5-88-88-88-41.6 0-68.5 21.3-103 68.3-34.5-47-61.4-68.3-103-68.3-48.5 0-88 39.5-88 88 0 14.5 3.8 27.9 10.1 40H32c-17.7 0-32 14.3-32 32v80c0 8.8 7.2 16 16 16h480c8.8 0 16-7.2 16-16v-80c0-17.7-14.3-32-32-32zm-326.1 0c-22.1 0-40-17.9-40-40s17.9-40 40-40c19.9 0 34.6 3.3 86.1 80h-86.1zm206.1 0h-86.1c51.4-76.5 65.7-80 86.1-80 22.1 0 40 17.9 40 40s-17.9 40-40 40z" />
          </svg>
        </button>
        <Popover isOpen={popGift} target="gift" placement="bottom">
          <PopoverBody>
            <h1>hola</h1>
          </PopoverBody>
        </Popover>
      </div>
    )
  }
};

export default Gift;
